import React from 'react'
import './TopBar.css'

export default ({ color, link, bgColor, icon, children }) => (
  <div
    className={`section-bar mb-4 ${bgColor}`}
    style={{ backgroundColor: bgColor }}
  >
    <div className="container">
      <div className="row">
        <div className="col-6">
          <h1
            className="section-title"
            style={{ color }}
            onClick={() => window.history.back()}
          >
            <i className="fa fa-arrow-left mr-3" />
            {children}
          </h1>
        </div>
        <div className="col-6 flex-wrap">
          <a
            href={link}
            target="_blank"
            title={link ? `Go to ${children}` : null}
          >
            <i className={`fa ${icon} section-icon`} style={{ color }} />
          </a>
        </div>
      </div>
    </div>
  </div>
)



